/*********************************** Header **************************************/
.header {
  padding: 35px; /* some padding */
  text-align: center; /* center the text */
  /* background: #3372c3be; green background */
  background-image:url('data:image/jpeg;base64,');
  /* background-repeat: no-repeat;
  background-attachment: fixed; */
  color: rgba(230, 245, 16, 0.829); /* white text color */
}

/* #header-background{
  width: 100%;
  height: 100px;
  background:url('data:image/jpeg;base64,');
} */
/* Increase the font size of the <h1> element */
.header h1 {
  font-size: 150px;
  font-style: bold;
  font-family: tangerine;
  text-shadow: rgba(16, 6, 105, 0.822) 1px 0 10px;
}

.header p {
  float: none;
  font-size: 35px;
  font-family: raleway;
} 
/******************************* Style the top navigation bar *******************************/
.navbar {
  /* overflow: hidden; Hide overflow */
  list-style: none;
  margin: 0 2px;
  padding: 0;
  display: flex;
  justify-content: space-around;
  background-color: #333; /* Dark background color */
}

/* Style the navigation bar links */
.navbar a {
  float: left; /* Make sure that the links stay side-by-side */
  display: block; /* Change the display to block, for responsive reasons (see below) */
  color: white; /* White text color */
  text-align: center; /* Center the text */
  padding: 10px 20px; /* Add some padding */
  text-decoration: none; /* Remove underline */
}

/* Right-aligned link */
.navbar a.right {
  float: right; /* Float a link to the right */
}

/* Change color on hover/mouse-over */
.navbar a:hover {
  background-color: rgb(25, 114, 77); /* Grey background color */
  color: black; /* Black text color */
}

/* Ensure proper sizing */
* {
  box-sizing: border-box;
}

/* Column container */
.row {
  display: flex;
  flex-wrap: wrap;
}

/******************************** Create two unequal columns that sits next to each other ***********************************/
/* Sidebar/ right column */
.side {
  flex: 30%;
  background-color: #f1f1f1;
  padding: 20px;
}

/************************* Main column *************************************************/
.main {   
  flex: 70%;
  background-color: white;
  /* background-image:url('data:image/jpeg;base64,'); */
  padding: 20px;
}

/****************************************** Fake Img **********************************/
.fakeimg {
  background-color: rgba(16, 6, 105, 0.822);
  width: auto;
  padding: none;
  background-repeat: no-repeat;
  background-image:url('data:image/jpeg;base64,');
  image-orientation: center;
}

/******************************* Makes the website Responsive *****************************/
/* Responsive layout - when the screen is less than 700px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 700px) {
  .row {
    flex-direction: column;
  }
}
/* Responsive layout - when the screen is less than 400px wide, make the navigation links stack on top of each other instead of next to each other */
@media screen and (max-width: 400px) {
  .navbar a {
    float: none;
    width: 100%;
  }
}
/************************ Footer Styling **********************************/
.footer {
  padding: 20px; /* Some padding */
  text-align: center; /* Center text*/
  background-color: #3372c3be; /* green background */
  font-size: 25px;
  font-family: raleway;
  color: rgba(230, 245, 16, 0.829); /* white text color */

}